@import './base/base';
@import './utils/variables';
@import './utils/mixins';
@import './utils/functions';
@import './layout/header';
@import './components/btn.scss';
@import './components/icon.scss';
@import './components/new.scss';
@import '../SASS/page/restaurants';
@import './base/reset';

//hero//
/*hero*/
.hero {
    @include flex-center;
    flex-direction: column;
    box-sizing: border-box;
    margin-block: 3rem;

    .hero__title {

        margin-inline-start: 2.5rem;
        margin-inline-end: 3.5rem;
        margin-bottom: 0.8rem;
        text-align: center;
    }

    .hero__subtitle {
        text-align: center;
        font-weight: 400;
        margin-inline-start: 2rem;
        margin-inline-end: 1.6rem;
        margin-bottom: 2rem;
    }


}

//fonctionnement//
/*fonctionnement*/
.notice {
    @include flex-center;
    flex-direction: column;
    align-items: flex-start;
    margin-inline: toRem(25);

    .notice__title {

        padding-bottom: toRem(26);

    }

    .notice-step {
        @include flex-center;
        flex-direction: column;
        gap: toRem(15);
        width: 100%;

        .notice-step__process {
            @include flex-center;
            justify-content: flex-start;
            gap: toRem(15);
            border-radius: toRem(20);
            height: toRem(70);
            min-width: 100%;
            @include shadow-bx;
            cursor: pointer
        }

    }
}

.notice-step__process:hover {
    color: $primary-color;

    & .mobile-screen .list--ul .store:hover {
        color: $primary-color;
    }

}

.notice-step__nbr {
    border-radius: toRem(25);
    background-color: $primary-color;
    color: #fff;
    width: toRem(24);
    height: toRem(24);
    text-align: center;
    position: relative;
    right: toRem(11);
    @include flex-center;
}


//restaurant//
/*restaurant*/
.restaurant__main-content {
    display: flex;
    flex-direction: column;
    gap: toRem(18);
    margin-inline: toRem(18);
    padding: 60px 0px;


}

.card-restaurant {
    @include box-style;
    @include shadow-bx;
    width: auto;
    border: none;
    cursor: pointer;
    position: relative;

    .img {
        border: none;
        height: min(124px, 231px);
        border-top-left-radius: toRem(15);
        border-top-right-radius: toRem(15);

    }


}

.card-restaurant__content {
    @include flex-center;
    justify-content: space-between;
    gap: toRem(5);
    padding-inline: toRem(12);
    margin-bottom: toRem(12);

    .card-restaurant__icon {

        @include flex-center;


    }

    .card_restaurant__subtitle {
        font-weight: 300;
    }
}


.footer {
    @include flex-center;
    flex-direction: column;
    align-items: flex-start;
    padding: 22px 25px;
    background-color: #353535;
    color: white;
    box-sizing: border-box;
    gap: toRem(5);

    .logo-text {
        font-family: $font-title;
        font-weight: 400;

    }

    .footer__list {
        padding: toRem(5);
        gap: toRem(5);
        @include flex-center;
        align-items: flex-start;
        flex-direction: column;

        .footer__list--item {
            gap: toRem(7);
        }



        .footer__list--item {
            @include flex-center;
        }
    }
}